@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
/* Import Roboto */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body div.container {
  display: flex;
  overflow-x: hidden;
}

body div.container img#pokeballIcon {
  width: 250px;
  height: 250px;
  position: absolute;
  opacity: 0.1;
  top: -100px;
  left: calc(50% - 125px);
}
body div.container h1.title {
  margin: 15px;
}
div#Index {
  flex: 1 1;
  min-height: 100vh;
  background-color: white;
}
div#Index div#items {
  display: flex;
  width: 96%;
  flex-wrap: wrap;
  margin-left: 2%;
}
div#mainMenu {
  height: 64px;
  display: flex;
  padding: 0 20px;
  justify-content: space-between;
  align-items: center;
}
div#mainMenu a img {
  width: 20px;
}
a.IndexItem {
  position: relative;
  --margin: 5px;
  box-sizing: border-box;
  width: calc(50%  - 2 * (var(--margin)));
  margin: var(--margin);
  padding: 15px;
  border-radius: 5px;
  font-size: 12pt;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
}
a.IndexItem > img {
  position: absolute;
  right: 0;
  width: 40px;
  height: 40px;
  -webkit-filter: invert(1);
          filter: invert(1);
  opacity: 0.6;
}
div#NotFound {
  text-align: center;
  width: 100%;
}

div#NotFound .App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

div#NotFound .App-logo {
  height: 40vmin;
}

div#NotFound header h1 {
  margin: 0px;
}

div#NotFound .App-link {
  color: #09d3ac;
}

@-webkit-keyframes up-card {
  0% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  50% {
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
  }
  100% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
}
@keyframes up-card {
  0% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  50% {
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
  }
  100% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
}
div.pokeCard {
  position: relative;
  height: 130px;
  --margin: 5px;
  box-sizing: border-box !important;
  width: calc(50% - 2 *(var(--margin)));
  margin: var(--margin);
  background-color: #4ad1b2;
  padding: 15px;
  border-radius: 5px;
  font-size: 12pt;
  color: white;
  text-decoration: none;
  display: flex;
  flex-direction: column;
}
div.pokeCard:hover {
  -webkit-animation-name: up-card;
          animation-name: up-card;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}
div.pokeCard strong {
  margin-bottom: 5px;
  text-transform: capitalize;
}
div.pokeCard p {
  position: absolute;
  right: 5px;
  color: #00000040
}
div.pokeCard span.pokeType {
  font-size: 13px;
  text-align: center;
  margin-bottom: 5px;
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  padding: 5px;
  background-color: #ffffff40;
  border-radius: 5px;
  text-transform: capitalize;
}
div.pokeCard img {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 50%;
}
div#pokeList {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
div#pokeList button {
  height: 60px;
  --margin: 5px;
  outline: none;
  border: 0px;
  width: calc(100% - 2 *(var(--margin)));
  margin: var(--margin);
  background-color: #4ad1b2;
  padding: 15px;
  border-radius: 5px;
  font-size: 12pt;
  color: white;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px #30a086;
}
div#pokeList button:active {
  box-shadow: 0px 0px #30a086;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}
div#Pokedex {
  flex: 1 1;
}
